import createGlobe from "cobe";

let phi = 0;
let canvas = document.getElementById("cobe");

const globe = createGlobe(canvas, {
  devicePixelRatio: 2,
  width: 1000,
  height: 1000,
  phi: 0,
  theta: 0,
  dark: 0.8,
  diffuse: 1,
  mapSamples: 8000,
  mapBrightness: 5,
  baseColor: [0, 0.3, 0],
  markerColor: [0.1, 0.5, 0.1],
  glowColor: [0, 0, 0],
  markers: [
    // longitude latitude
    //{ location: [37.7595, -122.4367], size: 0.1 },
    //{ location: [32.7595, 34.4367], size: 0.1 },
    //{ location: [40.7128, -74.006], size: 0.1 }
  ],
  onRender: (state) => {
    // Called on every animation frame.
    // `state` will be an empty object, return updated params.
    state.phi = phi;
    phi += 0.004;
  }
});
